.activity {
  width: 1180px;
  margin: 18px auto;
  border-radius: 4px;
  position: relative;
  padding-bottom: 30px;
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.038);
}
.activity .activity__pic {
  width: 1180px;
  height: 300px;
  border-radius: 4px;
  overflow: hidden;
}
.activity .activity__pic > img {
  width: 100%;
  height: 300px;
}
.activity .activity__info {
  font-size: 0;
  position: relative;
}
.activity .activity__info .activity__auhter {
  display: inline-block;
  vertical-align: top;
}
.activity .activity__info .activity__header {
  margin: -64px 22px 0 22px;
  width: 124px;
  height: 124px;
  background-color: #fff;
  border: 4px solid rgba(255, 255, 255, 0.4);
  background-clip: content-box;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
  z-index: 333;
}
.activity .activity__info .activity__header > a {
  display: block;
  width: 100%;
}
.activity .activity__info .activity__header  img {
  width: 100%;
}
.activity .activity__info .activity__name {
  display: inline-block;
  width: 166px;
  text-align: center;
  font-size: 14px;
  color: black;
}
.activity .activity__info .activity__desc {
  display: inline-block;
  vertical-align: top;
  margin-top: 26px;
}
.activity .activity__info .activity__desc > h6 {
  font-size: 22px;
  color: black;
  display: inline-block;
  vertical-align: middle;
  margin-right: 20px;
}
.activity .activity__info .activity__desc .activity__hot {
  display: inline-block;
}
.activity .activity__info .activity__desc .activity__hot > i {
  display: inline-block;
  font-size: 12px;
  vertical-align: middle;
  height: 22px;
  padding: 0 10px;
  background-color: #e66988;
  color: #fff;
  line-height: 22px;
  text-align: center;
  border-radius: 2px;
}
.activity .activity__info .activity__desc .activity__sub {
  font-size: 16px;
  color: #666;
  margin-top: 16px;
  text-indent: 8px;
  width: 800px;
  max-height: 100px;
  overflow: hidden;
}
.activity .activity__info .activity__desc .activity__sub p{
  display: none;
}
.activity .activity__info .activity__desc .activity__sub p:nth-of-type(1),
.activity .activity__info .activity__desc .activity__sub p:nth-of-type(2){
  display: block;
}
.activity .activity__info .activity__desc .activity__sub table tr{
  display: none;
}
.activity .activity__info .activity__desc .activity__sub table tr:nth-of-type(1),
.activity .activity__info .activity__desc .activity__sub table tr:nth-of-type(2){
  display: block;
}
.activity .activity__state {
  width: 138px;
  height: 90px;
  background-size: cover;
  position: absolute;
  top: 0;
  right: 20px;
}
.activity .activity__state > h6 {
  font-size: 22px;
  color: #fff;
  text-align: center;
  margin-top: 20px;
}
.activity .activity__state > p {
  font-size: 14px;
  color: #fff;
  text-align: center;
  line-height: 36px;
}

.activity .activity__state-jx {
  background: url("../css/../../components/Common/images/act_jx.png") no-repeat;
}
.activity .activity__state-sh {
  background: url("../css/../../components/Common/images/act_sh.png") no-repeat;
}
.activity .activity__state-js {
  background: url("../css/../../components/Common/images/act_js.png") no-repeat;
}
.activity .activity__state-yr {
  background: url("../css/../../components/Common/images/act_yr.png") no-repeat;
}



