
.clearfix:after{content:".";display:block;height:0;visibility:hidden;clear:both
}
.clearfix{zoom:1
}
.pact-detail__desc-info>dd{margin-bottom:26px
}
.pact-detail__desc-info>dd>h6{font-size:16px;margin-bottom:26px
}
.pact-detail__desc-info>dd>section{line-height:22px;font-size:14px;color:#666
}
.pact-detail__desc-info .pact-detail__desc-tips img{width:100%
}
.pact-detail__desc-info .pact-detail__desc-tips>section{overflow:hidden
}
.pact-detail__desc-info .pact-detail__desc-tips>section>p{line-height:22px;font-size:14px;color:#666
}
.pact-detail__desc-info .pact-detail__desc-pic>section>img{width:100%
}
.pact-detail__desc-info .pact-detail__desc-btn{text-align:center
}
.pact-detail__desc-info .pact-detail__desc-btn>i{display:block;margin:0 auto;width:48px;height:48px;background:url("./images/share@icon.png") no-repeat center;cursor:pointer;text-indent:-999em
}
.pact-detail__desc-info .pact-detail__desc-btn>em{display:inline-block;width:120px;height:50px;background-color:#f2708f;font-size:14px;border-radius:4px;line-height:50px;color:#fff;margin-top:12px;cursor:pointer;background-image:linear-gradient(to left, #e86987, #ff7a99);box-shadow:4px 4px 8px rgba(235,107,137,0.32);box-shadow:0 4px 6px 0 rgba(242,112,143,0.3)
}
.pact-detail__input>textarea{width:794px;height:118px;border:1px solid #e6e6e6;resize:none;border-radius:2px;font-size:14px;display:inline-block;margin:0 auto
}
.pact-detail__input .pact-detail__input-btn{text-align:right
}
.pact-detail__input .pact-detail__input-btn>i{display:inline-block;width:128px;height:48px;text-align:center;color:#fff;line-height:48px;border-radius:4px;font-size:14px;margin-top:12px;cursor:pointer;box-shadow:0 4px 6px 0 rgba(242,112,143,0.3);background-image:linear-gradient(to left, #e86987, #ff7a99);box-shadow:4px 4px 8px rgba(235,107,137,0.32)
}



